const endpoint = "https://www.alliancefitnesscenter.com/graphql";
// const endpoint = "https://afcdevnewslettersubscribev2.local/graphql";


export const getWPGraphQLData = (query: string) => {
  return (
    fetch(endpoint, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ query })
    })
      .then((response) => {
        if (response.status >= 400) {
          throw new Error("Error fetching data");
        } else {
          return response.json();
        }
      })
      .then((data) => data.data)
  )
}


